<template>
  <div>
    <div v-for="p in paginatedData" :key="p.id">
      <div class="post">
        <h3>{{ p.title }}</h3>
        <div class="post-wrap">
          <img
            :src="p.img"
            alt=""
            v-on:click="getDetail(p.id), toggleDetail(true), toggleTitle(true)"
          />
          <div class="post-excerpt">
            <p>{{ p.excerpt }}</p>
            <div class="line"></div>
            <ul class="post-meta">
              <li class="author">
                <i class="icon icon-user"></i>{{ p.author }}
              </li>
              <li class="date">
                <i class="icon icon-calendar"></i>{{ p.date }}
              </li>
              <li class="comment">
                <i class="icon icon-chat-empty"></i>{{ p.comment }} comments
              </li>
              <li class="tags"><i class="icon icon-tags"></i>{{ p.tags }}</li>
            </ul>
          </div>
        </div>
      </div>
    </div>

    <div class="btn">
      <button :disabled="pageNumber === 0" @click="prevPage">Previous</button>
      <button :disabled="pageNumber >= pageCount - 1" @click="nextPage">
        Next
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "PaginationList",
  props: {
    listData: {
      type: Array,
      required: true,
    },
    size: {
      type: Number,
      required: false,
      default: 3,
    },
  },
  data() {
    return {
      pageNumber: 0,
    };
  },

  methods: {
    nextPage() {
      this.pageNumber++;
    },
    prevPage() {
      this.pageNumber--;
    },
    getHTML() {
      this.$parent.getRatingHTML();
    },
    getDetail(id) {
      this.$parent.getDetail(id);
    },
    toggleDetail(show) {
      this.$parent.toggleDetail(show);
    },
    toggleTitle(show) {
      this.$parent.toggleTitle(show);
    },
  },
  computed: {
    pageCount() {
      let l = this.listData.length,
        s = this.size;
      return Math.ceil(l / s);
    },
    paginatedData() {
      const start = this.pageNumber * this.size,
        end = start + this.size;
      return this.listData.slice(start, end);
    },
  },
};
</script>
