<template>
  <section id="blog">
    <div class="container">
      <div>
        <h2>Blog</h2>
        <p class="description">
          Praesent blandit mollis dui a imperdiet. Vestibulum eu vehicula ante.
          Curabitur lacinia ipsum et porta ultrices. Morbi eu nunc convallis,
          ullamcorper ipsum nec
        </p>
        <div class="blog_wrap">
          <div class="content">
            <div :class="{ show: showPosts }" id="posts-item">
              <PaginationList :listData="posts_list" />
            </div>

            <div :class="{ show: showDetail }" id="post-item">
              <div
                class="arrow_back"
                v-on:click="toggleDetail(false), toggleTitle(false)"
              >
                <svg>
                  <use xlink:href="@/assets/img/svg/sprite.svg#arrow"></use>
                </svg>
              </div>
              <PostItem :itemData="post_item" />

              <div class="btn">
                <button v-on:click="prevPage()">Previous</button>
                <button v-on:click="nextPage()">Next</button>
              </div>
              <div id="form-title" class="form-title">
                THERE ARE {{ post_item.comment }} COMMENTS ON THIS POST
              </div>

              <PostComment :postComment="post_item.comments" />

              <div class="form-title">LEAVE YOUR COMMENT TO THIS POST</div>
              <form v-on:submit.prevent="addNewComment">
                <input
                  v-model="newCommentName"
                  id="name-comment"
                  placeholder="Name"
                  required
                />
                <input
                  v-model="newCommentEmail"
                  id="email-comment"
                  placeholder="Email"
                  required
                />
                <textarea
                  v-model="newCommentText"
                  id="text-comment"
                  placeholder="Comment"
                  maxlength="160"
                  required
                ></textarea>

                <button>Submit comment</button>
              </form>
            </div>
          </div>
          <aside>
            <h3>Upcoming events</h3>
            <div class="events">
              <div class="event-item">
                <div class="date">30 jan</div>
                <div class="excerpt">
                  Nunc in lobortis ipsum. In porta, nulla nec vehicula
                  scelerisque, risus augue pellentesque est, in scelerisque.
                  <!--<a href="#">Read more</a>-->
                </div>
              </div>
              <div class="event-item">
                <div class="date">05 feb</div>
                <div class="excerpt">
                  Sed vitae molestie erat. Fusce eget erat in augue lacinia
                  dapibus. Nam erat ante, adipiscing convallis eros sed.
                  <!--<a href="#">Read more</a>-->
                </div>
              </div>
            </div>
          </aside>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import Vue from "vue";
import axios from "axios";
import VueAxios from "vue-axios";
import PostComment from "@/components/PostComment.vue";
import PaginationList from "@/components/PaginationList.vue";
import PostItem from "@/components/PostItem.vue";
Vue.use(VueAxios, axios);

export default {
  components: {
    PostComment,
    PaginationList,
    PostItem,
  },
  data() {
    return {
      posts_list: [],
      post_item: [],
      showDetail: true,
      showPosts: true,
      newCommentText: "",
      newCommentEmail: "",
      newCommentName: "",
      nextCommentId: 1,
      date: "",
    };
  },
  mounted() {
    let posts = localStorage.getItem("posts");
    if (posts != null) {
      this.posts_list = JSON.parse(posts);
      this.toggleDetail(false);
    } else {
      const url = "assets/json/posts_list.json";
      axios.get(url).then((response) => {
        this.posts_list = response.data;
        this.getHTML();
        this.toggleDetail(false);
      });
    }
    let months = [
      "January",
      "February",
      "March",
      "April",
      "May",
      "June",
      "July",
      "August",
      "September",
      "October",
      "November",
      "December",
    ];
    let myDate = new Date();
    let fullDate =
      months[myDate.getMonth()] +
      " " +
      myDate.getDate() +
      ", " +
      myDate.getFullYear();
    this.date = fullDate;
  },
  methods: {
    getHTML() {
      for (let i = 0; i < this.posts_list.length; i++) {
        let item = this.posts_list[i];
        item.comment = this.posts_list[i].comments.length;
        if (item.excerpt.length >= 150) {
          item.excerpt = item.excerpt.substring(0, 150);
          let lastIndex = item.excerpt.lastIndexOf(" "); // позиция последнего пробела
          item.excerpt = item.excerpt.substring(0, lastIndex) + "..."; // обрезаем до последнего слова
        }
      }
    },
    getDetail(id) {
      for (let i = 0; i < this.posts_list.length; i++) {
        let item = this.posts_list[i];
        item.comment = this.posts_list[i].comments.length;
        if (item.id == id) {
          this.post_item = item;
        }
      }
    },
    toggleDetail(show) {
      this.showDetail = show;
      document.getElementById("post-item").style.display = show
        ? "block"
        : "none";
      this.showPosts = show;
      document.getElementById("posts-item").style.display = show
        ? "none"
        : "block";
    },
    nextPage() {
      let newItem = this.post_item.id + 1;
      for (let i = 0; i < this.posts_list.length; i++) {
        let item = this.posts_list[i];
        if (item.id == newItem) {
          this.post_item = item;
        }
      }
      this.toggleTitle();
    },
    prevPage() {
      let newItem = this.post_item.id - 1;
      if (newItem != 0) {
        for (let i = 0; i < this.posts_list.length; i++) {
          let item = this.posts_list[i];
          if (item.id == newItem) {
            this.post_item = item;
          }
        }
      }
      this.toggleTitle();
    },
    addNewComment() {
      this.post_item.comments.push({
        id: this.nextCommentId++,
        name: this.newCommentName,
        email: this.newCommentEmail,
        text: this.newCommentText,
        date: this.date,
      });
      for (let i = 0; i < this.posts_list.length; i++) {
        let item = this.posts_list[i];
        if (this.post_item.id == item.id) {
          item = this.post_item;
        }
      }
      this.getDetail(this.post_item.id);
      this.newCommentName = "";
      this.newCommentEmail = "";
      this.newCommentText = "";
      this.toggleTitle();
      localStorage.setItem("posts", JSON.stringify(this.posts_list));
    },
    toggleTitle() {
      if (this.post_item.comment != "") {
        document.getElementById("form-title").style.display = "block";
      } else {
        document.getElementById("form-title").style.display = "none";
      }
    },
  },
};
</script>



